export default {
	data() {
		return {
			currentSwiper: 0
		}
	},
	props: {
		options: Object
	},
	methods: {
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({ title: e.src, icon: 'none' });
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		}
	}
}

